.promo {
  max-height: max-content;
  padding-bottom: 45px;

  @media (max-width: $tablet-width-only) {
    padding-bottom: 155px;
  }

  @media (max-width: $mobile-width-only) {
    padding-bottom: 73px;
  }
}

.promo__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 718px 705px;
  column-gap: 63px;

  @media (max-width: 1486px) {
    column-gap: 7px;
  }

  @media (max-width: $tablet-width-only) {
    grid-template-columns: 426px 1fr;
  }

  @media (max-width: $mobile-width-only) {
    grid-template-columns: 1fr;
  }
}

.promo__container {
  @media (max-width: $mobile-width-only) {
    order: 1;
  }
}

.promo__img-wrapper {
  position: relative;
  width: 705px;
  height: 515px;

  @media (max-width: $tablet-width-only) {
    width: 465px;
    height: 340px;
  }

  @media (max-width: $mobile-width-only) {
    justify-self: flex-end;
    width: 301px;
    height: 220px;
    z-index: -1;
  }
}

.promo__img-wrapper img {
  top: -60px;
  left: 9px;
  object-fit: contain;
  display: block;
  z-index: 2;
  position: relative;
  width: 100%;
  height: 100%;

  @media (max-width: $tablet-width-only) {
    top: -80px;
    left: 50px;
  }

  @media (max-width: $mobile-width-only) {
    top: 25px;
    left: 15px;
  }
}

.promo__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $basic-white;
  margin: 0;
  margin-bottom: 28px;

  @media (max-width: $tablet-width-only) {
    font-size: 60px;
    line-height: 70px;
    padding-left: 60px;
    max-width: 320px;
    margin-bottom: 32px;
  }

  @media (max-width: $mobile-width-only) {
    padding-left: 0;
    font-size: 40px;
    line-height: 47px;
    margin-bottom: 25px;
  }
}

.promo__img-wrapper::after {
  content: "";
  position: absolute;
  right: 70px;
  top: -115px;
  width: 618px;
  height: 637px;
  background-color: $basic-white;
  mask-repeat: no-repeat;
  mask-position: center;
  mask-image: url("../img/stack.svg#promo-svg");
  mask-size: cover;
  z-index: -1;

  @media (max-width: $tablet-width-only) {
    right: -20px;
    top: -110px;
    width: 490px;
    height: 400px;
  }

  @media (max-width: $mobile-width-only) {
    right: -320px;
    top: -43px;
    width: 465px;
    height: 465px;
  }
}

.promo__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: $basic-white;
  box-sizing: border-box;
  width: 777px;
  padding-left: 94px;
  margin-bottom: 44px;
  position: relative;


  @media (max-width: $tablet-width-only) {
    font-size: 18px;
    line-height: 156.8%;
    width: 510px;
    margin-left: 20px;
    margin-bottom: 51px;
    display: block;
  }

  @media (max-width: $mobile-width-only) {
    width: 93vw;
    margin-left: 0;
    font-size: 16px;
    padding-left: 55px;
    line-height: 156.8%;
    margin-bottom: 42px;
  }
}

.promo__subtitle::before {
  content: "";
  position: absolute;
  left: 0;
  top: 15px;
  width: 66px;
  height: 2px;
  background-color: $basic-white;
  mask-repeat: no-repeat;
  mask-position: center;
  mask-image: url("../img/stack.svg#hide");
  mask-size: cover;

  @media (max-width: $tablet-width-only) {
    width: 33px;
    left: 45px;
    top: 11px;
  }

  @media (max-width: $mobile-width-only) {
    left: 5px;
    top: 17px;
  }
}

.promo__link {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $basic-white;
  text-decoration: none;
  width: 407px;
  padding: 16px;
  box-sizing: border-box;
  border: 1px solid $basic-blue;
  background-color: $basic-blue;
  border-radius: 5px;
  display: block;
  margin-left: 94px;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }

  @media (max-width: $desktop-width) {
    width: 380px;
    margin-left: 111px;
  }

  @media (max-width: $mobile-width-only) {
    width: 93vw;
    margin: 0;
  }
}

.promo__link-mobile {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
  }
}

.promo__link-desktop {
  display: block;

  @media (max-width: $tablet-width) {
    display: none;
  }
}
